/* Layout */
.headerpanel {
  background: #20242d;
}

.headerpanel .logo {
  display: inline-block;
  padding: 0 20px;
}

.headerpanel .logo h1 {
  line-height: 60px;
  text-align: center;
  margin: 0;
}

.headerpanel .logo h1 a {
  font-weight: 300;
  color: #ebebeb;
}

.headerpanel .logo h1 a:hover {
  color: #ebebeb;
}

.headerpanel .logo h1 a span {
  font-weigth: 700 !important;
}

.headerpanel .headerbar {
  float: right;
}

.headerpanel .headerbar .menutoggle i {
  margin-top: -15px;
  margin-left: -12px;
}

.headerpanel .headerbar .searchpanel {
  left: 15px;
}

.headerpanel .headerbar .searchpanel .input-group .btn {
  padding-top: 10px;
  padding-right: 13px;
}

.headerpanel .headerbar .header-right .headermenu .item-menu {
  padding: 0 15px;
  height: 64px;
  line-height: 64px;
  position: relative;
}

.headerpanel .headerbar .header-right .headermenu .item-menu a {
  color: #778395;
}

.headerpanel .headerbar .header-right .headermenu .item-menu a i {
  font-size: 16px;
}

.headerpanel .headerbar .header-right .headermenu .item-menu.active {
  background: rgba(255, 255, 255, 0.04);
}

.headerpanel .headerbar .header-right .headermenu .item-menu .with-notification {
  position: absolute;
  bottom: 23px;
  right: 14px;
  height: 7px;
  width: 7px;
  background: #c9302c;
  border-radius: 100%;
}

.headerpanel .headerbar .header-right .headermenu .btn-group .dropdown-menu li.active > a {
  background-color: #ddd;
}

.mainpanel {
  margin-left: 90px;
}

.mainpanel .contentpanel {
  padding: 0;
}

.leftpanel {
  width: 90px;
  background: #262b36;
}

.leftpanel .menu li.active a {
  background: #20242d;
  border-color: #259dab;
}

.leftpanel .menu li.active a i {
  border-color: #259dab;
}

.leftpanel .menu li.active a i:before {
  background: #259dab;
}

.leftpanel .menu li a {
  border-left: 5px solid transparent;
  padding: 12px 5px 12px 0;
  display: block;
  text-align: center;
  transition: all .2s;
}

.leftpanel .menu li a:hover {
  background: #20242d;
}

.leftpanel .menu li a i {
  display: block;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 100%;
  width: 38px;
  height: 38px;
  line-height: 34px;
  text-align: center;
  font-size: 20px;
  color: #262b36;
  margin: 0 auto;
}

.leftpanel .menu li a i:before {
  background: rgba(255, 255, 255, 0.7);
  border-radius: 100%;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
}

.leftpanel .menu li a span {
  display: block;
  color: #fff;
  text-align: center;
  margin-top: 6px;
  font-size: 10px;
  font-weight: 300;
}

.breadcrumb.breadcrumb-quirk {
  display: inline-block;
}

.breadcrumb.breadcrumb-quirk > li i {
  margin-top: -3px;
  margin-right: 3px;
  vertical-align: middle;
}

.breadcrumb.breadcrumb-quirk > li + li:before {
  font-family: 'Material Design Icons';
  content: "\F142";
}

#toolbar h2 .selected {
  font-style: italic;
  font-size: 11px;
}

#toolbar .options .btn-toolbar .btn.btn-quirk {
  font-weight: 400;
}

#toolbar .options .toolbar-title {
  font-size: 20px;
  margin: 0 0 0 20px;
  line-height: 38px;
  font-weight: 300;
  color: #444;
  display: inline-block;
}

#toolbar .options .divider {
  background: #ccc;
  width: 1px;
  display: inline-block;
  margin: 0 20px;
  height: 38px;
  vertical-align: middle;
}

#toolbar .options .paginate .counter {
  color: #999;
  display: inline-block;
  font-style: italic;
}

#toolbar .options .paginate .btn-group {
  margin-left: 20px;
  display: inline-block;
}

#toolbar .options .paginate .btn-group .btn {
  border-color: transparent;
  margin-left: 1px;
  height: 38px;
  width: 38px;
  position: relative;
}

#toolbar .options .paginate .btn-group .btn:first-child {
  margin-left: 0;
}

#toolbar .options .paginate .btn-group .btn i {
  font-size: 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

#toolbar .options .paginate .limit {
  color: #666;
  display: inline-block;
  vertical-align: middle;
}

#toolbar .options .paginate .limit select {
  display: inline-block;
  width: 70px;
  margin-right: 10px;
}

.page p {
  line-height: 18px;
}

.fieldset-enter-active {
  -webkit-animation: fadeInUp .2s;
  animation: fadeInUp .2s;
}

.fieldset-leave-active {
  -webkit-animation: fadeOut .2s;
  animation: fadeOut .2s;
}

.fade-enter-active {
  -webkit-animation: fadeIn .5s;
  animation: fadeIn .5s;
}

.fade-leave-active {
  -webkit-animation: fadeOut .5s;
  animation: fadeOut .5s;
}

.flipX-enter-active {
  -webkit-animation: flipInX .5s;
  animation: flipInX .5s;
}

.slideDown-leave-active {
  -webkit-animation: flipOutX .5s;
  animation: flipOutX .5s;
}

.tab-enter-active {
  -webkit-animation: fadeIn .3s;
  animation: fadeIn .3s;
}

.tab-leave-active {
  display: none;
}

.modal-enter-active {
  -webkit-animation: fadeIn .3s;
  animation: fadeIn .3s;
}

.modal-leave-active {
  -webkit-animation: fadeOut .3s;
  animation: fadeOut .3s;
}

.modalBox-enter-active {
  -webkit-animation: slideInDown .3s;
  animation: slideInDown .3s;
}

.modalBox-leave-active {
  -webkit-animation: slideOutUp .3s;
  animation: slideOutUp .3s;
}

.autocomplete-enter-active {
  -webkit-animation: fadeInUp .3s;
  animation: fadeInUp .3s;
}

.autocomplete-leave-active {
  -webkit-animation: fadeOut .3s;
  animation: fadeOut .3s;
}

.dropdown-enter-active {
  -webkit-animation: flipInX .3s;
  animation: flipInX .3s;
}

.dropdown-leave-active {
  -webkit-animation: fadeOut .3s;
  animation: fadeOut .3s;
}

.toolbar-enter-active {
  -webkit-animation: fadeIn .5s;
  animation: fadeIn .5s;
}

.toolbar-leave-active {
  display: none;
}

.folders-enter-active {
  -webkit-animation: flipInX .3s;
  animation: flipInX .3s;
}

.folders-leave-active {
  display: none;
}

.uploader-enter-active {
  -webkit-animation: bounceInRight .3s;
  animation: bounceInRight .3s;
}

.uploader-leave-active {
  -webkit-animation: bounceOutRight .3s;
  animation: bounceOutRight .3s;
}

.notifications-enter-active {
  -webkit-animation: slideInRight .3s;
  animation: slideInRight .3s;
}

.notifications-leave-active {
  -webkit-animation: slideOutRight .3s;
  animation: slideOutRight .3s;
}

.top-nav {
  margin: 4px 0 0;
}

@-webkit-keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

/* SCROLLBAR PERSONALIZADO */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(0, 0, 0, 0.3);
}

