}

/* Components */
table.table {
  margin-bottom: 0;
}

table.table thead tr th {
  background: #fff;
  padding: 10px;
  border-bottom: 3px solid #efefef;
  vertical-align: middle;
}

table.table tbody tr td {
  background: #fff;
  padding: 10px;
  border-bottom: 1px solid #efefef;
  vertical-align: middle;
}

table.table tbody tr td .el-checkbox {
  margin-bottom: 0;
}

table.table tbody tr:last-child td {
  border-bottom: 0;
}

table.table-contacts {
  color: #262b36;
}

table.table-contacts thead tr th {
  vertical-align: top;
}

table.table-contacts thead tr th.folder i {
