#nprogress {
  z-index: 999999999;
}

#nprogress .bar,
#nprogress .spinner {
  z-index: 999999999999;
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid transparent;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
}

.loader.button {
  width: 13px;
  height: 13px;
  border-width: 2px;
  vertical-align: middle;
  margin-top: -3px;
}

.loader.inline {
  display: inline-block;
}

.loader.light {
  border-top-color: transparent;
}

.page-loading {
  display: block;
  margin: 30px auto 0;
  text-align: center;
}

.page-loading .loader {
  vertical-align: middle;
  width: 30px;
  height: 30px;
  border-width: 1px;
  border-color: #464f63;
  border-top-color: transparent;
  margin-right: 10px;
}

.page-loading span {
  color: #464f63;
  display: inline-block;
  font-size: 20px;
  font-weight: 300;
  vertical-align: middle;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

