/* TIMELINE */
.timeline {
  list-style: none;
  position: relative;
  padding: 0;
}

.timeline.align-left:before {
  left: 18.5px;
}

.timeline.align-left > li > .timeline-badge {
  left: 18.5px;
}

.timeline.align-left > li > .timeline-panel {
  width: calc(100% - 75px);
  float: right;
}

.timeline.align-left > li > .timeline-panel:before {
  border-left-width: 0;
  border-right-width: 15px;
  left: -15px;
  right: auto;
}

.timeline.align-left > li > .timeline-panel:after {
  border-left-width: 0;
  border-right-width: 13px;
  left: -13px;
  right: auto;
}

.timeline.align-right:before {
  right: 18.5px;
  left: initial;
}

.timeline.align-right > li > .timeline-badge {
  left: initial;
  right: 0;
}

.timeline.align-right > li > .timeline-panel {
  width: calc(100% - 75px);
}

.timeline.align-center > li:nth-child(2n) > .timeline-panel {
  float: right;
}

.timeline.align-center > li:nth-child(2n) > .timeline-panel:before {
  border-left-width: 0;
  border-right-width: 15px;
  left: -15px;
  right: auto;
}

.timeline.align-center > li:nth-child(2n) > .timeline-panel:after {
  border-left-width: 0;
  border-right-width: 14px;
  left: -14px;
  right: auto;
}

.timeline:before {
  top: 0;
  bottom: 0;
  position: absolute;
  content: " ";
  width: 3px;
  background-color: #eeeeee;
  left: 50%;
  margin-left: -1.5px;
}

.timeline > li {
  margin-top: 20px;
  position: relative;
}

.timeline > li:before, .timeline > li:after {
  content: '';
  display: table;
}

.timeline > li:after {
  clear: both;
}

.timeline > li > .timeline-badge {
  color: #fff;
  width: 40px;
  height: 40px;
  line-height: 40px;
  font-size: 1.2em;
  text-align: center;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -20px;
  background-color: #999999;
  z-index: 100;
  border-top-right-radius: 50%;
  border-top-left-radius: 50%;
  border-bottom-right-radius: 50%;
  border-bottom-left-radius: 50%;
}

.timeline > li > .timeline-badge.primary {
  background-color: #259dab !important;
}

.timeline > li > .timeline-badge.success {
  background-color: #2ecc71 !important;
}

.timeline > li > .timeline-badge.warning {
  background-color: #e6ad5c !important;
}

.timeline > li > .timeline-badge.danger {
  background-color: #c9302c !important;
}

.timeline > li > .timeline-badge.info {
  background-color: #3498db !important;
}

.timeline > li .timeline-panel {
  width: calc(50% - 50px);
  float: left;
  border: 1px solid #d4d4d4;
  border-radius: 2px;
  padding: 15px;
  position: relative;
  border-radius: 3px;
  background: #f8f8f8;
}

.timeline > li .timeline-panel:before {
  position: absolute;
  bottom: 10px;
  right: -15px;
  display: inline-block;
  border-top: 15px solid transparent;
  border-left: 15px solid #ccc;
  border-right: 0 solid #ccc;
  border-bottom: 15px solid transparent;
  content: " ";
}

.timeline > li .timeline-panel:after {
  position: absolute;
  bottom: 11px;
  right: -14px;
  display: inline-block;
  border-top: 14px solid transparent;
  border-left: 14px solid #f8f8f8;
  border-right: 0 solid #f8f8f8;
  border-bottom: 14px solid transparent;
  content: " ";
}

.timeline > li .timeline-panel .timeline-heading .timeline-title {
  margin-top: 0;
  color: inherit;
  font-weight: 400;
  font-size: 14px;
}

.timeline > li .timeline-panel .timeline-heading p {
  margin: 0;
}

.timeline > li .timeline-panel .timeline-body {
  margin: 20px 0 0;
}

.timeline > li .timeline-panel .timeline-body > p,
.timeline > li .timeline-panel .timeline-body > ul {
  margin-bottom: 0;
  font-size: 12px;
}

.timeline > li .timeline-panel .timeline-body > p + p {
  margin-top: 5px;
}

/* PROGRESS BAR */
.progress-load {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: inline-block;
}

.progress-load .circle .mask, .progress-load .circle .fill, .progress-load .circle .shadow {
  width: 20px;
  height: 20px;
  position: absolute;
  border-radius: 50%;
}

.progress-load .circle .mask, .progress-load .circle .fill {
  -webkit-backface-visibility: hidden;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}

.progress-load .circle .mask {
  clip: rect(0px, 20px, 20px, 10px);
}

.progress-load .circle .mask .fill {
  clip: rect(0px, 10px, 20px, 0px);
  background-color: #2ecc71;
}

.progress-load .inset {
  width: 16px;
  height: 16px;
  position: absolute;
  margin-left: 2px;
  margin-top: 2px;
  background-color: #fff;
  border-radius: 50%;
}

